<template>
  <div id="root">
    <div class="container">
      <div class="display-4 my-4 text-center"> Mikrotik Test </div>

      <QuestionBox 
        v-if="questions.length"
        :currentQuestion="questions[index]"
        :next="next"
        :increment="increment"
      />

      <div v-if="index === 1">
        <div class="modal fade">
          AAA
        </div>
      </div>

      <div class="text-center my-4">
        <b-button-group>
          <b-button variant="primary"> Give Up! </b-button>
          <router-link class="btn btn-primary" to="/About"> About Me </router-link>
        </b-button-group>
      </div>
    </div>

    <b-modal ref="my-modal" hide-footer title="Yeah">
      <div class="d-block text-center">
        <h3> Selesai :)</h3>
        <p class="lead"> 
          Anda telah mengerjakan soal ini dan mendapatkan <code>{{ numCorrect }}</code> jawaban benar dari 10 soal. 

          <br>
          Untuk menghargai pembuat, silahkan kunjungi <code> <router-link class="btn btn-secondary" to="/About"> Tentang Saya </router-link>. </code>  
        </p>
      </div>


      <router-link class="btn btn-primary btn-block" to="/Choose"> Kembali ke Soal </router-link>
      <router-link class="btn btn-primary btn-block" to="/About"> Tentang Saya </router-link>
      <b-button class="mt-3" variant="outline-danger" block @click="hideModal"> Tutup </b-button>
    </b-modal>
  </div>
</template>

<script>
import QuestionBox from '@/components/QuestionBox.vue'

export default {
  props:['id'],
  name: 'Question',
  components: {
    QuestionBox
  },
  data() {
    return {
      questions: [],
      index: 0,
      numCorrect: 0,
      numTotal: 0,
      param: 0,
      jsonSoal: "",
      api: [
        "https://raw.githubusercontent.com/naufaladrna08/jsons/master/soal-1.json",
        "https://raw.githubusercontent.com/naufaladrna08/jsons/master/soal-2.json"
      ]
    }
  },
  methods: {
    next() {
      this.index++
    },
    increment(isCorrect) {
      if (isCorrect) {
        this.numCorrect++
      }

      if (this.index >= 9) {
        this.showModal()
      }

      this.numTotal++
    },
    showModal() {
      this.$refs['my-modal'].show()
    },
    hideModal() {
      this.$refs['my-modal'].hide()
    },
    APIgetJSON() {
      this.param = this.$route.params.id
      
      if (this.param == 1) {
        return this.api[0]
      } else {
        return this.api[1]
      }
    }
  },
  mounted: function() {
    fetch(this.APIgetJSON(), {
      method: 'get'
    })

    .then((response) => {
      return response.json()
    })

    .then((jsonData) => {
      this.questions = jsonData.results
    })
  }
}
</script>

